body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: rgb(235, 215, 179);
}
ul li{
  color: black;
  border: 2px solid black;
  list-style: none;
  width: 250px;
  justify-content: center;
  text-align: center;
  padding: 5px 5px ;
  margin: 6px auto;
  border-radius: 5px;
  background-color: rgb(208, 233, 149);
  font-weight: 600;
  flex-direction: row;
  
}
ul{
  text-align: center;
  /* display: flex; */
  width: 100%;
}
.App{
  text-align: center;
}
button{
  border: 2px solid black;
  border-radius:10px ;
  width: 100px;
  height: 30px;
  background-color: rgb(203, 114, 114);
  font-weight: 800;
}
button:hover{
  background-color: rgb(222, 67, 67);
  
}
h2{
  color: black;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

